*{

    color: azure;
}

.container{

    width: 60%;

    margin: 50px auto;

    border: 1px solid red;

    padding: 40px;


}

.form{
    border: 1px solid azure;

    padding: 20px;

    margin: 20px auto ;

    display: flex;
    align-items: center;

    gap:20px
}

.form input{

    padding: 10px;

    color: black;

    outline: none;

    width: 250px;
}

.form button{

    padding: 10px 20px;

    background: green;
}

.todo-items{

    width: 60%;

    display: flex;
    justify-content: space-between;

    margin-bottom: 20px;
    margin-top: 30px;

    border: 1px solid azure;

    padding: 20px 10px;

}

.todo-items p{

    font-size: 20px;
}

.todo-button{

    background: rgb(243, 7, 7);

    padding: 15px 10px;
}